import React from 'react'
import { Link } from 'gatsby'
import styled from 'styled-components'
import Logo from '../../static/logos/logo.png'

const Header = styled.header`
  background: ${props => props.theme.colors.base};
  width: 100%;
  padding: 1em 0;
`
const Nav = styled.nav`
  width: 100%;
  height: 20px;
  max-width: ${props => props.theme.sizes.maxWidth};
  margin: 0 auto;
  padding: 0 1.5em;

  .logo {
    max-width: 240px;
    min-width: 80px;
    margin-top: -7px;
    border: 1px solid lightsteelblue;
    background: rgba(255, 255, 255, 0.9);
  }

  ul {
    display: flex;
    justify-content: space-between;
  }

  li {
    display: inline-block;
    margin-left: 1em;
    &:first-child {
      position: relative;
      margin: 0;
      flex-basis: 100%;
    }
  }

  a {
    text-decoration: none;
    color: DarkGray;
    font-weight: 600;
    transition: all 0.2s;
    &:hover {
      color: yellow;
    }
  }
`

const activeLinkStyle = {
  color: 'white',
}

const Menu = () => {
  return (
    <Header>
      <Nav>
        <ul>
          <li>
            <Link to="/" activeStyle={activeLinkStyle}>
            <img className="logo" src={Logo} />
            </Link>
          </li>
          <li>
            <Link to="/" activeStyle={activeLinkStyle}>
              AnaSayfa
            </Link>
          </li>
          <li>
            <Link to="/hakkimizda/" activeStyle={activeLinkStyle}>
              Hakkımızda
            </Link>
          </li>
          <li>
            <Link to="/ekibimiz/" activeStyle={activeLinkStyle}>
              Ekibimiz
            </Link>
          </li>
          <li>
            <Link to="/hizmetlerimiz/" activeStyle={activeLinkStyle}>
              Hizmetlerimiz
            </Link>
          </li>
          <li>
            <Link to="/contact/" activeStyle={activeLinkStyle}>
              İletişim
            </Link>
          </li>
        </ul>
      </Nav>
    </Header>
  )
}

export default Menu
